// import { Fragment } from 'react';
import { useNavigate } from 'react-router-dom';
import {
	NavigatorStyled,
	LinkStyled,
	LogoutButton,
	GroupStyled,
	MemberStyled,
} from './navigator.styled';

// import styled from 'styled-components';

function Navigator({ collapsed }) {
	const navigate = useNavigate();
	const staffData = JSON.parse(localStorage.getItem('memberApp'));

	// 
	const handleLogout = () => {
		localStorage.removeItem('memberApp');
		navigate('/login'); // 
	};

	return (
		<NavigatorStyled $collapsed={collapsed}>
			{staffData ? (
				<GroupStyled>
					<MemberStyled>{staffData.email}</MemberStyled>
					<LogoutButton onClick={handleLogout}></LogoutButton>
				</GroupStyled>
			) : (
				<GroupStyled>
					<LinkStyled to="/login">/</LinkStyled>
				</GroupStyled>
			)}
			<GroupStyled>
				<LinkStyled to="/dashboard/member-change"></LinkStyled>
				<LinkStyled to="/dashboard/member-change"></LinkStyled>
				<LinkStyled to="/dashboard/member-change"></LinkStyled>
			</GroupStyled>
		</NavigatorStyled>
	);
}

export default Navigator;
